<template>
    <div>
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                
                <el-button type="primary" icon="plus" @click="handleAdd()">新建</el-button>
            </el-form>
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="position_name" label="岗位名称" width="120">
                </el-table-column>
                <el-table-column prop="remark" label="备注" width="120">
                </el-table-column>
                <el-table-column prop="user_name" label="创建人" width="120">
                </el-table-column>
                <el-table-column prop="create_time" label="创建时间">
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button type="text" icon="el-icon-edit" @click="handleEdit(row, $index)">修改</el-button>
                        <el-button type="text" icon="el-icon-delete" class="red" @click="handleDelete(row, $index)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="limit">
                </el-pagination>
            </div> -->
        </div>

        <!-- 编辑添加 -->
        <el-dialog :title="dialogTitle" :visible.sync="dialogPost" width="50%">
            <el-form ref="formPost" :model="formPost" label-width="80px" :rules="rules">
                <el-form-item label="岗位名称" prop="position_name">
                    <el-input v-model.trim="formPost.position_name" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="备注">
                    <el-input v-model.trim="formPost.remark" placeholder="请输入"></el-input>
                </el-form-item>
            </el-form>
            
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogPost = false">取 消</el-button>
                <el-button type="primary" @click="dialogSubmitPost">确 定</el-button>
            </span>
        </el-dialog>
    
    </div>
</template>

<script>
    import * as API from '@/api/enterpriseManagement'
    export default {
        name: 'basetable',
        data() {
            return {
                tableData: [],
                page: 1,
                total: 1,
                limit: 10,
                loading: false,
                dialogTitle: '',
                dialogPost: false,
                formPost: {
                    position_name:'',
                    remark:''
                },
                filters: {
                    movieID:'',
                    movieName:'',
                    value:'3'
                },
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }],
                rules:{
                    position_name:[
                        {required: true, message: '请输入岗位名称', trigger:'blur'}
                    ],
                    remark:[
                        {required: true, message: '请输入备注', trigger:'blur'}
                    ]
                }
            }
        },
        created() {
            this.getData();
        },
        computed: {
            
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
               let params = {
                    page: this.page
                }
                this.loading = true
                API.postManagementList(params).then(res => {
                    this.loading = false
                    this.tableData = res.data;
                    this.total = res.total;
                })
               
            },
            handleAdd(){
                this.dialogPost = true;
                this.dialogTitle = '添加';
                Object.assign(this.formPost, {
                    position_name:'',
                    remark:''
                })
                
            },
            handleEdit(row, index) {
                this.dialogPost = true;
                this.dialogTitle = '编辑';
                const item = this.tableData[index];
                Object.assign(this.formPost,row)
                
            },
            handleDelete(row, index) {
                API.postManagementDel({id: row.id}).then(res => {
                    this.tableData.splice(index, 1);
                })
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            dialogSubmitPost(){
                this.$refs.formPost.validate((valid)=>{
                    if(valid){
                        if(this.dialogTitle == '添加'){
                            API.postManagementAdd(this.formPost).then(res => {
                                this.dialogPost = false;
                                this.getData();
                            })
                        }else{
                            API.postManagementEdit(this.formPost).then(res => {
                                this.dialogPost = false;
                                this.getData();
                            })
                        }
                    }else{
                        return false
                    }
                })
                
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
</style>
